label {
  margin-bottom: 0.75rem; }

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/* Links */
a {
  color: #00cc00;
  text-decoration: none; }
  a:visited {
    color: #00cc00; }
  a:hover, a:focus, a:active {
    color: #090; }
  a:focus {
    outline: thin dotted; }
  a:hover, a:active {
    outline: 0; }

/* Menus */
.main-navigation {
  clear: both;
  display: flex;
  float: left;
  min-width: 550px;
  width: 100%;
  flex-direction: column;
  margin: 4.5rem;
  margin-left: 0; }
  @media (min-width: 620px) {
    .main-navigation {
      flex-direction: row;
      justify-content: flex-start; } }
  @media (min-width: 1080px) {
    .main-navigation {
      justify-content: flex-end; } }
  .main-navigation p {
    padding-top: .25rem;
    padding-left: .75rem;
    margin: 0;
    line-height: 1; }
  .main-navigation ul {
    display: none;
    list-style: none;
    margin: 0;
    padding-left: 0; }
    .main-navigation ul ul {
      box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
      float: left;
      position: absolute;
      top: 1.5rem;
      left: -999rem;
      z-index: 99999; }
      .main-navigation ul ul ul {
        left: -999rem;
        top: 0; }
      .main-navigation ul ul li:hover > ul,
      .main-navigation ul ul li.focus > ul {
        left: 100%; }
      .main-navigation ul ul a {
        width: 200px; }
    .main-navigation ul li:hover > ul,
    .main-navigation ul li.focus > ul {
      left: auto; }
  .main-navigation li {
    float: left;
    position: relative;
    padding: 0 0.75rem 0 0.75rem;
    border-right: 1px solid #fff; }
  .main-navigation a {
    text-decoration: none; }

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
  display: block; }

@media screen and (min-width: 350px) {
  .menu-toggle {
    display: none; }
  .main-navigation ul {
    display: block; } }

.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
  margin: 0 0 1.5rem;
  overflow: hidden; }

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
