/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
.site-main {
  position: relative; }
  .site-main i {
    display: none; }
  @media (min-width: 1080px) {
    .site-main::before, .site-main::after {
      position: absolute;
