      font-variant: normal;
      text-rendering: auto;
      font-size: 125px;
      font-weight: 400; }
    .site-main::before {
      content: '\f10d';
      left: -175px;
      line-height: 1; }
    .site-main::after {
      content: '\f10e';
      right: -175px;
      line-height: 1;
      top: 0px; } }

